.header {
    position: absolute;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;

    text-decoration: none;

    margin-top: 5%;
    height: 75px;
    width: 100vw;
}


.header .right {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    margin-right: 50px;
}
