body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Open Sans', 'Lato',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@-moz-document url-prefix() {
  .background {
    background: radial-gradient(farthest-corner at 40px 40px,
      #6076C4 0%, #C460A5 75%, #28235F 100% );
  }
}

.header {
    position: absolute;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;

    text-decoration: none;

    margin-top: 5%;
    height: 75px;
    width: 100vw;
}


.header .right {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    margin-right: 50px;
}

.button {
    display: flex;
    flex-direction: row;

    align-items: right;
    justify-content: right;

    height: 3.125em;
    width: 9.375em;

    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.15em;
    text-align: right;

    color: #000000;

    background: transparent;

    margin-right: 25px;
}

.button:hover {
    font-weight: 600;
}

.picture-button {
    height: 350px;
    width: 250px;

    box-shadow: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.05));

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;

    margin-right: 15px;
    margin-bottom: 15px;

    background: red;

}

.picture-button:hover {
    -webkit-filter: brightness(110%);
            filter: brightness(110%);
}

.picture-button .imageContainer {
    height: 300px;
    width: 250px;
}

.picture-button .textContainer .subtitle {
    font-family: Lato;
    font-size: 14px;
    font-style: italic;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.15em;
    text-align: left;

    margin-top: 10px;
    margin-bottom: 5px;

    color: #000000;
}

.picture-button .textContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    height: 100px;
    width: 250px;
}

.landing {
    height: 100vh;

    display: flex;

    align-items: center;
    justify-content: center;

    flex-direction: row;

    background: #FAFAFA;
    background-size: cover;
}

.landing .text-container {
    width: 40vw;

    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;
}

.landing .text-container .big-text {
    font-family: Lato;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 43px;
    letter-spacing: 0.15em;
    text-align: left;
}

.landing .text-container .text {
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 0px;
    letter-spacing: 0.15em;
    text-align: left;
}

.landing .text-container .text-bold {
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 0px;
    letter-spacing: 0.15em;
    text-align: left;
}

.landing .text-container .text-italic {
    font-family: Lato;
    font-size: 14px;
    font-style: italic;
    font-weight: 400;
    line-height: 0px;
    letter-spacing: 0.15em;
    text-align: left;
}

.landing .button-container {
    width: 30vw;

    display: flex;

    align-items: center;
    justify-content: center;

    flex-direction: row;
}

.landing .button-container .image {
  height: 350px;
  width: 250px;
  object-fit: fill;
}

.about {
    height: 100%;

    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: row;

    background: #FAFAFA;
    background-size: cover;
}

.about .text-container {
