@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);

.landing {
    height: 100vh;

    display: flex;

    align-items: center;
    justify-content: center;

    flex-direction: row;

    background: #FAFAFA;
    background-size: cover;
}

.landing .text-container {
    width: 40vw;

    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;
}

.landing .text-container .big-text {
    font-family: Lato;
    font-size: 36px;
    font-style: normal;
    font-weight: 700;
    line-height: 43px;
    letter-spacing: 0.15em;
    text-align: left;
}

.landing .text-container .text {
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 0px;
    letter-spacing: 0.15em;
    text-align: left;
}

.landing .text-container .text-bold {
    font-family: Lato;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 0px;
    letter-spacing: 0.15em;
    text-align: left;
}

.landing .text-container .text-italic {
    font-family: Lato;
    font-size: 14px;
    font-style: italic;
    font-weight: 400;
    line-height: 0px;
    letter-spacing: 0.15em;
    text-align: left;
}

.landing .button-container {
    width: 30vw;

    display: flex;

    align-items: center;
    justify-content: center;

    flex-direction: row;
}

.landing .button-container .image {
  height: 350px;
  width: 250px;
  object-fit: fill;
}
